export const query = graphql`
    query IndexQuery {
        allMarkdownRemark {
            totalCount
            edges {
                node {
                    id
                    frontmatter {
                        title
                        date(formatString: "MMMM DD, YYYY")
                        path
                        image {
                            publicURL
                            childImageSharp {
                                fluid(maxWidth: 1200) {
                                    ...GatsbyImageSharpFluid,
                                }
                            }
                        }
                    }
                    excerpt
                }
            }
        }
    }
`;

export default class extends React.Component {
  render() {
    return (
      <Layout>
        テスト
      </Layout>
    );
  }
}
